<template>
  <div class="dialog-mask">
    <div class="dialog-box">
      <router-link to="/" class="close-btn" title="" replace>&times;</router-link>
      <div class="dialog-header"></div>
      <form action="" @submit.prevent="onLogin">
        <ul>
          <li>
            <span class="error-message iconfont" v-if="logInForm.emailError">{{logInForm.emailError}}</span>
            <label for="userID"><i class="iconfont icon-zhanghao"></i></label>
            <input type="text" name="userID" id="userID" placeholder="" v-model="logInForm.email">
          </li>
          <li>
            <span class="error-message iconfont" v-if="logInForm.passwdError">{{logInForm.passwdError}}</span>
            <label for="userID"><i class="iconfont icon-mima"></i></label>
            <input type="password" name="userPass" placeholder="" v-model="logInForm.passwd">
          </li>

          <li><button></button></li>
        </ul>
      </form>
      <div class="links-area">
        <a href="#"></a>
        <router-link to="signup" replace></router-link>
      </div>
    </div>
  </div>
</template>

<script>
import AV from 'leancloud-storage'
import router from '../router'
export default {
  data () {
    return {
      logInForm: {
        email: '',
        passwd: '',
        emailError: '',
        passwdError: ''
      }
    }
  },
  router: router,
  methods: {
    onLogin () {
      let emailError,
        passwdError

      if (!this.logInForm.email) {
        this.logInForm.emailError = ''
        return
      } else {
        this.logInForm.emailError = ''
      }

      if (!this.logInForm.passwd) {
        this.logInForm.passwdError = ''
        return
      } else {
        this.logInForm.passwdError = ''
      }

      AV.User.logIn(this.logInForm.email, this.logInForm.passwd).then((res) => {
        this.$router.replace('/')
        this.$parent.hasLogin()
      }, (error) => {
        switch (error.code) {
          case 210:
            passwdError = ''
            break
          case 211:
            emailError = ''
            break
          case 216:
            emailError = ''
            break
          case 219:
            passwdError = ''
            break
          default :
            passwdError = error.code + '' + error.error
        }
        this.logInForm.emailError = emailError
        this.logInForm.passwdError = passwdError
      })
    }
  }
}
</script>
